<template>
  <el-dropdown ref="dropdown" trigger="hover" :hide-on-click=false @visible-change="visibleChanged" size="small">
    <el-button type="primary" size="small">
      <span class="hidden-xs-only">登录权限上下文</span><span class="hidden-sm-and-up">权限</span><i class="el-icon-arrow-down el-icon--right"></i>
    </el-button>
    <el-dropdown-menu slot="dropdown" class="demo-menu-form-box">
      <el-form>
        <el-form-item v-for="ctxKey in ctxKeys" :key="ctxKey" :label="ctxKey">
          <el-select
            v-if="Object.prototype.toString.call(ctx.demoAuthCtx[ctxKey]) === '[object Array]'"
            v-model="ctx.demoAuthCtx[ctxKey]"
            multiple filterable allow-create default-first-option
            no-data-text="请输入，回车添加一个条目"
            placeholder="请输入多个值"
            style="width: 100%">
          </el-select>
          <el-input v-else v-model="ctx.demoAuthCtx[ctxKey]"></el-input>
        </el-form-item>
        <el-form-item label="authorityKeys">
          <el-select v-model="ctx.authorityKeys"
            multiple filterable allow-create default-first-option
            no-data-text="请输入，回车添加一个条目"
            placeholder="请输入多个值"
            style="width: 100%">
          </el-select>
        </el-form-item>
        <hr style="margin: 20px 0px">
        <el-form-item>
          <el-button type="primary" @click="onSubmit">设置 并 刷新页面</el-button>
          <el-button type="default" @click="onReset">重置</el-button>
        </el-form-item>
      </el-form>
    </el-dropdown-menu>
  </el-dropdown>
</template>

<script>
import 'element-ui/lib/theme-chalk/display.css'

export default {
  name: 'DemoLoginCtx',
  data () {
    return {
      initAuthorityKeys: ['XXX'],
      initSystemId: '',
      initDemoAuthCtx: {
        systemId: this.initSystemId,
        myAuId: 1,
        myLoginName: 'mr_zhang',
        myRealName: '张飞',
        myFirmId: 1
      },
      ctx: this.defaultCtx()
    }
  },
  computed: {
    ctxKeys: function () {
      let ctxKeys = []
      for (let ctxKey in this.ctx.demoAuthCtx) {
        ctxKeys.push(ctxKey)
      }
      return ctxKeys
    }
  },
  created () {
  },
  methods: {
    init: function (systemId, initAuthorityKeys, initDemoAuthCtx) {
      this.initSystemId = systemId
      if (initAuthorityKeys) {
        for (let i = 0; i < initAuthorityKeys.length; i++) {
          this.initAuthorityKeys.push(initAuthorityKeys[i])
        }
      }
      this.initDemoAuthCtx = Object.assign(this.initDemoAuthCtx, initDemoAuthCtx)
      let tmp = JSON.parse(window.localStorage.getItem('Demo.AdminAuthCtx.' + this.initSystemId))
      if (tmp && tmp.demoAuthCtx) {
        this.ctx = tmp
      } else {
        this.ctx = this.defaultCtx()
      }
      return this.ctx.demoAuthCtx.systemId
    },
    defaultCtx: function () {
      return {
        authorityKeys: this.initAuthorityKeys,
        demoAuthCtx: this.initDemoAuthCtx
      }
    },
    onSubmit: function () {
      window.localStorage.setItem('Demo.AdminAuthCtx.' + this.initSystemId, JSON.stringify(this.ctx))
      this.$emit('submit', this.ctx)
    },
    onReset: function () {
      window.localStorage.removeItem('Demo.AdminAuthCtx.' + this.initSystemId)
      this.ctx = this.defaultCtx()
      this.$emit('submit', this.ctx)
    },
    visibleChanged: function (visible) {
      if (visible) {
        //  trigger="hover" :hide-on-click=false的设置的情况下，下面语句能阻止按回车键隐藏对话框，否则不生效
        let dropdown = this.$refs.dropdown
        dropdown.triggerElm.removeEventListener('keydown', this.$refs.dropdown.handleTriggerKeyDown)
      }
    }
  }
}
</script>

<style scoped>
.demo-menu-form-box {
  background-color: #2A3F54;
  padding: 6px 1px;
}
.demo-menu-form-box .el-form {
  padding: 10px;
  background-color: #fff;
}
.el-form-item {
  margin-bottom: 6px;
}
.el-form-item__label {
  line-height: 32px;
}
</style>



